ul {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  grid-gap: 10px;
  list-style: none;
  margin: 0 10px 10px 10px;
}

h1 {
  text-align: center;
  color: #10ee78;
  text-transform: uppercase;
  margin: 10px;
  font-family: 'Varela Round';
}

img {
  display: block;
  max-width: 100%;
  height: 80%;
  max-height: 80%;
}

.card {
  border-radius: 5px;
  border: 2px solid #003c80;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  height: 80vh;
}

.card-desc {
  padding: 10px;
  -ms-flex: 1 1;
      flex: 1 1;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 1fr auto;
  grid-gap: 10px;
}

h2 {
  color: #2ba84a;
  cursor: pointer;
  text-transform: capitalize;
}

.card:hover h2 {
  text-decoration: underline;
}

.card:hover {
  border-color: #1f7ae2;
}



